/* eslint-disable no-debugger */
import { Component, Output, EventEmitter, Input } from '@angular/core';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { ImageCroppedEvent, ImageCropperModule, ImageTransform } from 'ngx-image-cropper';
import { ButtonModule } from 'primeng/button';
import { RippleModule } from 'primeng/ripple';
import { NgIf } from '@angular/common';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-image-cropper',
  standalone: true,
  imports: [ButtonModule, ImageCropperModule, TranslateModule, RippleModule, NgIf],
  templateUrl: './image-cropper.component.html',
  styleUrl: './image-cropper.component.scss',
})
export class ImageCropperComponent {
  @Input() fileInput: File | null = null;
  @Output() croppedFile = new EventEmitter<string>();

  transform: ImageTransform = {};
  scale = 1;

  constructor(
    private toast: MessageService,
    private translate: TranslateService,
  ) {}

  async onImageCropped(event: ImageCroppedEvent) {
    if (event.blob) {
      const base64Image = await this.blobToBase64(event.blob);
      this.croppedFile.emit(base64Image);
    }
  }

  async onFileInputChange(event: Event) {
    const target = event.target as HTMLInputElement;
    const fileList: FileList | null = target.files;
    if (fileList && fileList.length > 0) {
      if (!fileList[0].type.startsWith('image/')) {
        this.showErrorMessage('ERROR.WRONG_TYPE');
      } else {
        this.fileInput = fileList[0];
      }
    }
  }

  blobToBase64(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        resolve(reader.result as string);
      };
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });
  }

  /**
   * Rotates the image clockwise (clockwise) in 90 degree increments.
   * If the transformation is not defined, initializes it as an empty object.
   * Update the 'rotate' property on the transformation with a 90 degree rotation.
   */
  rotateRight() {
    this.transform = {
      ...this.transform,
      rotate: (this.transform.rotate || 0) + 90,
    };
  }

  /**
   * Increases the image zoom level by 10%.
   * Updates the 'scale' property in the transformation with the new zoom level.
   */
  zoomIn() {
    this.scale += 0.1;
    this.transform = {
      ...this.transform,
      scale: this.scale,
    };
  }

  /**
   * Reduces the image zoom level by 10%.
   * Updates the 'scale' property in the transformation with the new zoom level.
   */
  zoomOut() {
    if (this.scale > 1) {
      this.scale -= 0.1;
      this.transform = {
        ...this.transform,
        scale: this.scale,
      };
    }
  }

  /**
   * Flips the image horizontally.
   * Will toggle the 'flipH' property in the transform to horizontally mirror the image.
   */
  flipHorizontal() {
    this.transform = {
      ...this.transform,
      flipH: !this.transform.flipH,
    };
  }

  private showErrorMessage(key: string): void {
    this.toast.add({
      severity: 'error',
      summary: this.translate.instant('ERROR.TITLE'),
      detail: this.translate.instant(key),
    });
  }
}
